import { Component } from '@angular/core';
import { IonicPage, LoadingController, NavController, NavParams } from 'ionic-angular';
import { animate, style, transition, trigger } from '@angular/animations';

/**
 * Generated class for the ApplyCashlineMobPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-apply-cashline-mob',
  templateUrl: 'apply-cashline-mob.html',
  animations: [
    trigger(
      'boxAnimation',
      [
        transition(':enter',
          [style({ transform: 'translateX(-100%)', opacity: 0 }),
          animate('300ms', style({ transform: 'translateX(0)', 'opacity': 1 }))]),
        transition(':leave',
          [style({ transform: 'translateX(0)', 'opacity': 1 }),
          animate('300ms', style({ transform: 'translateX(100%)', 'opacity': 0 }))])
      ]),
    trigger(
      'simulasiAnimation',
      [
        transition(':enter',
          [style({ transform: 'translateY(100%)', opacity: 0 }),
          animate('300ms', style({ transform: 'translateY(0)', 'opacity': 1 }))]),
        transition(':leave',
          [style({ transform: 'translateY(0)', 'opacity': 1 }),
          animate('300ms', style({ transform: 'translateY(100%)', 'opacity': 0 }))])
      ])
  ]
})
export class ApplyCashlineMobPage {
  cc_ownership: boolean;
  cc_over_12_months: boolean;
  limit;
  limitA = 15000000;
  limitB = 30000000;
  isShowSimulasi: boolean = false;
  constructor(public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ApplyCashlineMobPage');
    document.documentElement.style.setProperty('--stepper-from', '3');
  }

  selectCc(type) {
    this.cc_ownership = type;

    if (this.isShowSimulasi) {
      if (type == false) {
        this.isShowSimulasi = false;

        let loading = this.loadingCtrl.create({
          content: '<div class="custom-loader-container"><div class="custom-loader-bar"></div></div>',
          spinner: 'dots',
          cssClass: 'msmile-loading'
          // dismissOnPageChange: true
        });

        loading.present();

        setTimeout(() => {
          if (this.cc_ownership == false) {
            this.limit = this.limitA;
          } else {
            this.limit = this.limitB;
          }
          this.isShowSimulasi = true;
          loading.dismiss()
        }, 1000);
      } else {
        this.isShowSimulasi = false;
      }
    }
  }

  selectCc2(type) {
    this.cc_over_12_months = type;

    if (this.isShowSimulasi) {
      this.isShowSimulasi = false;

      let loading = this.loadingCtrl.create({
        content: '<div class="custom-loader-container"><div class="custom-loader-bar"></div></div>',
        spinner: 'dots',
        cssClass: 'msmile-loading'
        // dismissOnPageChange: true
      });

      loading.present();

      setTimeout(() => {
        if (this.cc_over_12_months == false) {
          this.limit = this.limitA;
        } else {
          this.limit = this.limitB;
        }
        this.isShowSimulasi = true;
        loading.dismiss()
      }, 1000);
    }
  }

  showSimulasi() {
    if (this.cc_ownership == false || (this.cc_ownership == true && (this.cc_over_12_months == true || this.cc_over_12_months == false))) {
      let loading = this.loadingCtrl.create({
        content: '<div class="custom-loader-container"><div class="custom-loader-bar"></div></div>',
        spinner: 'dots',
        cssClass: 'msmile-loading'
        // dismissOnPageChange: true
      });

      loading.present();

      setTimeout(() => {
        this.limit = this.limitA;
        if (this.cc_ownership == false) {
          this.limit = this.limitA;
        } else {
          if (this.cc_over_12_months) {
            this.limit = this.limitB;
          }
        }
        this.isShowSimulasi = true;
        loading.dismiss()
      }, 1000);
    }

  }

}



